.btn-activity-edit{
    width: 1.7rem;
    height: 1.7rem;
    border: unset;
}

.activity-container{
    overflow-y: auto;
}

.activity-container.split{
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
}

.activity-container .nist-buttons{
    display: flex;
    gap: 0.25rem;
    flex-wrap: wrap;
}

.activity-container .nist-buttons .p-button{
    padding: 0.25rem;
    border: 1px solid #0000002e;
}

.act-header{
    font-size: small;
    font-weight: bold;
    margin-bottom: 0.5rem;
}

.activity-main ol{
    margin: unset;
}

.activity-main .grid{
    margin-bottom: 0.5rem;
}

.activity-nist, .activity-main{
    display: flex;
    flex-direction: column;
    height: 100%;
}

.activity-nist .p-toggleable-content, .activity-main .p-toggleable-content{
    overflow-y: auto;
}

.activity-main .p-panel-title{
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
    align-items: center;
}

.activity-nist .p-panel-title{
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 0.5rem;
    width: 100%;
}

.activity-main.hide{
    display: none;
}

.activity-main.show{
    display: flex;
}

.activity-nist.hide{
    display: none;
}

.nist-control-link:hover{
    cursor: pointer;
    text-decoration: underline;
}